.dropdownBlock {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.1), 0px 4px 8px rgba(0, 0, 0, 0.1),
    0px 2px 2px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  width: 10rem;
  align-items: flex-start;
  padding: 1rem;
  background-color: #fff;
}
.hideDropdown {
  display: none;
}
